import React from 'react';
import { Good } from './types/Good';

type Props = {
  goods: Good[]
  errorMessage: string
};

export const GoodsList: React.FC<Props> = ({ goods, errorMessage }) => (
  <ul>
    {errorMessage
      ? <li>{errorMessage}</li>
      : goods.map(good => (
        <li
          key={good.id}
          data-cy="good"
          style={{ color: good.color }}
        >
          {good.name}
        </li>
      ))}
  </ul>
);
